<template>
  <div>
    <van-cell
      :to="$root.isBindPhone ? '' : '/user_bind_phone'"
      :is-link="!$root.isBindPhone"
      title="手机号码"
    >
      {{ $root.isBindPhone ? $root.userInfo.phone : "未绑定手机号码" }}
    </van-cell>
    <van-cell
      is-link
      title="登录密码"
      @click="checkIfSetUsername('/user_login_psw', $root.isBindPwd)"
    >
      {{ $root.isBindPwd ? "修改登录密码" : "设置登录密码" }}
    </van-cell>
    <van-cell
      is-link
      title="资金密码"
      @click="
          checkIfSetUsername('/user_pay_psw', $root.userInfo.secPasswordStatus)
        "
    >
      {{ $root.userInfo.secPasswordStatus ? "修改资金密码" : "设置资金密码" }}
    </van-cell>
    <van-cell title="谷歌身份验证器">
      <van-switch size="small" :value="isGoogleAuthOpen" @input="onGoogleAuthInput"></van-switch>
    </van-cell>
  </div>
</template>

<script>
import {Switch} from 'vant'

export default {
  components: {
    [Switch.name]: Switch
  },
  computed: {
    isGoogleAuthOpen() {
      return this.$root.userInfo.isGoogleAuthenticator === 1
    }
  },
  created() {
    this.$root.$emit("root:set-topbar", ["left-back", "customer-service"]);
  },
  methods: {
    onGoogleAuthInput(checked) {
      if (checked) {
        this.$router.push('/bind_google_auth')
      } else {
        this.$router.push('/unbind_google_auth')
      }
    },
    checkIfSetUsername(path, isBind) {
      if(this.$root.registerMode === '1') {
        if (this.$root.isSetUserName) {
          this.$router.push(path);
        } else {
          return this.$dialog.alert({
            message: "请设置用户名!",
            className: "common",
            beforeClose:(action, done) => {
              done()
              this.$router.push('/user_name')
            }
          });
        }
      }
      if(this.$root.registerMode === '2') {
        if (this.$root.isBindPhone) {
          this.$router.push(path);
        } else {
          return this.$dialog.alert({
            message: "请绑定手机号!",
            className: "common",
            beforeClose:(action, done) => {
              done()
              this.$router.push('/user_bind_phone')
            }
          });
        }
      }
    },
  }
}
</script>
